*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}
input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;

}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid #202020;
  border-radius: 6px;
  height: auto;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width:auto;
}
.card p {
  padding: 5px;
  text-align: center;
}